.login,.register{display:flex;align-items:center;justify-content:center;position:absolute;left:0;top:0;right:0;bottom:0}.login .login-form,.login .registration-form,.register .login-form,.register .registration-form{width:100%;max-width:400px}.login .email,.login .pass1,.login .pass2,.login .password,.login .username,.register .email,.register .pass1,.register .pass2,.register .password,.register .username{display:block;width:100%;height:32px;margin-bottom:20px}.login .reset-link,.register .reset-link{display:block;margin-bottom:20px}